import { FETCH_COMPANY_LIST_SUCCESS } from "../../constants/ActionTypes";

const initialUsers = {
  companyList: null,
};

export default (state = initialUsers, action) => {
  switch (action.type) {
    case FETCH_COMPANY_LIST_SUCCESS: {
      return {
        ...state,
        companyList: action.payload,
      };
    }

    default:
      return state;
  }
};
