import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { Card, Table, Button } from "antd";

import { abuseReportListFetch } from "../../redux/actions/AbuseReport";

class AbuseReport extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
    };

    this.columns = [
      {
        title: "ID",
        dataIndex: "id",
        width: "5%",
      },
      {
        title: "Resason",
        dataIndex: "reason",
        width: "10%",
      },
      {
        title: "Created At",
        dataIndex: "createdAt",
        width: "10%",
      },
      {
        title: "Actions",
        dataIndex: "update",
        width: "2%",
        render: (update, item) => {
          let id = item.id;
          return (
            <div key={id}>
              <Link to={{ pathname: `/abuseReport/view/${id}`, data: { item } }}>
                <Button type="primary" size="small">
                  View
                </Button>
              </Link>
            </div>
          );
        },
      },
    ];
  }

  componentDidMount() {
    this.props.abuseReportListFetch();
  }

  render() {
    return (
      <Card className="gx-card" title="Abuse Report List">
        <Table
          columns={this.columns}
          size={"small"}
          dataSource={this.props.abuseReportList || []}
          rowKey={(record) => record.id}
          pagination={false}
          loading={this.state.loading}
          bordered
        />
      </Card>
    );
  }
}

const mapStateToProps = ({ abuseReport }) => {
  const { abuseReportList } = abuseReport;
  return { abuseReportList };
};

export default connect(
  mapStateToProps,
  { abuseReportListFetch }
)(AbuseReport);
