import { FETCH_JOB_LIST_SUCCESS } from "../../constants/ActionTypes";
import APICaller from "../../utils/APICaller";
import * as APIs from "../../constants/APIs";

export const jobListFetch = () => {
  return (dispatch) => {
    APICaller.execute(APIs.GET_JOB_LIST).then((response) => {
      dispatch({
        type: FETCH_JOB_LIST_SUCCESS,
        payload: response.data
      })
    })
  };
};

export const jobFetch = (id) => {
  return (dispatch) => {
    return APICaller.execute(APIs.GET_JOB(id)).then((response) => {
      return response.data;
    }).catch((error) => {
      throw error;
    })
  };
};
