import React from "react";
import { connect } from "react-redux";
import { Card, Table } from "antd";

import { skillListFetch } from "../../redux/actions/Data";

class Skill extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
    };

    this.columns = [
      {
        title: "ID",
        dataIndex: "id",
        width: "5%",
      },
      {
        title: "Skill",
        dataIndex: "title",
        width: "50%",
      },
    ];
  }

  componentDidMount() {
    this.props.skillListFetch();
  }

  render() {
    return (
      <Card className="gx-card" title="Skill List">
        <Table
          columns={this.columns}
          size={"small"}
          dataSource={this.props.skillList || []}
          rowKey={(record) => record.id}
          pagination={false}
          loading={this.state.loading}
          bordered
        />
      </Card>
    );
  }
}

const mapStateToProps = ({ data }) => {
  const { skillList } = data;
  return { skillList };
};

export default connect(
  mapStateToProps,
  { skillListFetch }
)(Skill);
