import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { Card, Table, Button } from "antd";

import { companyListFetch } from "../../redux/actions/Company";

class Company extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
    };

    this.columns = [
      {
        title: "ID",
        dataIndex: "id",
        width: "5%",
      },
      {
        title: "Company Name",
        dataIndex: "name",
        width: "50%",
      },
      {
        title: "Type",
        dataIndex: "type",
        width: "10%",
      },
      {
        title: "Actions",
        dataIndex: "update",
        width: "5%",
        render: (update, item) => {
          let id = item.id;
          return (
            <div key={id}>
              <Link to={{ pathname: `/company/view/${id}`}}>
                <Button type="primary" size="small">
                  View
                </Button>
              </Link>
            </div>
          );
        },
      },
    ];
  }

  componentDidMount() {
    this.props.companyListFetch();
  }

  render() {
    return (
      <Card className="gx-card" title="Company List">
        <Table
          columns={this.columns}
          size={"small"}
          dataSource={this.props.companyList || []}
          rowKey={(record) => record.id}
          pagination={false}
          loading={this.state.loading}
          bordered
        />
      </Card>
    );
  }
}

const mapStateToProps = ({ company }) => {
  const { companyList } = company;
  return { companyList };
};

export default connect(
  mapStateToProps,
  { companyListFetch }
)(Company);
