import { FETCH_JOB_LIST_SUCCESS } from "../../constants/ActionTypes";

const initialUsers = {
  jobList: null,
};

export default (state = initialUsers, action) => {
  switch (action.type) {
    case FETCH_JOB_LIST_SUCCESS: {
      return {
        ...state,
        jobList: action.payload,
      };
    }

    default:
      return state;
  }
};
