import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { Card, Table, Button } from "antd";

import { jobListFetch } from "redux/actions/Job";

class Job extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
    };

    this.columns = [
      {
        title: "Job ID",
        dataIndex: "id",
        width: "5%",
      },
      {
        title: "Job Title",
        dataIndex: "jobTitle",
        width: "20%",
      },
      {
        title: "Company Name",
        dataIndex: "company.name",
        width: "20%",
      },
      {
        title: "Actions",
        dataIndex: "update",
        width: "5%",
        render: (update, post) => {
          let id = post.id;
          return (
            <div key={id}>
              <Link to={{ pathname: `/job/view/${id}`, data: { post } }}>
                <Button type="primary" size="small">
                  View
                </Button>
              </Link>
            </div>
          );
        },
      },
    ];
  }

  componentDidMount() {
    this.props.jobListFetch();
  }

  render() {
    return (
      <Card className="gx-card" title="Jobs List">
        <Table
          columns={this.columns}
          size={"small"}
          dataSource={this.props.jobList || []}
          rowKey={(record) => record.id}
          pagination={false}
          loading={this.state.loading}
          bordered
        />
      </Card>
    );
  }
}

const mapStateToProps = ({ job }) => {
  const { jobList } = job;
  return { jobList };
};

export default connect(
  mapStateToProps,
  { jobListFetch }
)(Job);
