import React from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { Col, Row, message, Card } from "antd";

import { Auxiliary, Spin } from "utils";

import { companyFetch } from "../../redux/actions/Company";
import {createRenderingData} from '../../utils'

class ViewAbuseReport extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: "",
      abuseReport: null,
      notFound: false,
      loading: true,
    };
  }

  async componentDidMount() {
    try {
      let abuseReport = this.props.location.data.item;

      this.setState({
        id: this.props.match.params.id,
        abuseReport,
        loading: false,
      });
    } catch (error) {
      message.error("Company data not found.");
      this.setState({ notFound: true });
    }
  }

  render() {
    const { abuseReport } = this.state;

    const toRenderData = [];

    createRenderingData(toRenderData, abuseReport);

    if (!this.state.notFound) {
      if (this.state.loading) {
        return (
          <Spin tip="Please wait ..." spinning={this.state.loading}></Spin>
        );
      } else {
        return (
          <Auxiliary>
            <div>
              <Row>
                <Col xl={24} lg={24} md={24} sm={48} xs={48}>
                  {abuseReport ? (
                    <Card key={abuseReport.id} title={abuseReport.id}>
                      {toRenderData.map((data) => (
                        <p key={data.key}>
                          <span className="gx-fs-sm gx-font-weight-medium">
                            {data.key}:
                          </span>
                          <span className="gx-ml-2 gx-fs-sm gx-font-weight-light">
                            {data.value}
                          </span>
                        </p>
                      ))}
                    </Card>
                  ) : (
                    <p>company data not available</p>
                  )}
                </Col>
              </Row>
            </div>
          </Auxiliary>
        );
      }
    } else {
      return <Redirect to={"/company/list"} />;
    }
  }
}

const mapStateToProps = ({ auth }) => {
  const { loader, alertMessage, showMessage, authUser } = auth;
  return { loader, alertMessage, showMessage, authUser };
};

export default connect(
  mapStateToProps,
  {
    companyFetch,
  }
)(ViewAbuseReport);
