import {
  FETCH_INTEREST_LIST_SUCCESS,
  FETCH_SKILL_LIST_SUCCESS,
} from "../../constants/ActionTypes";

const initialUsers = {
  skillList: null,
  interestList: null,
};

export default (state = initialUsers, action) => {
  switch (action.type) {
    case FETCH_SKILL_LIST_SUCCESS: {
      return {
        ...state,
        skillList: action.payload,
      };
    }

    case FETCH_INTEREST_LIST_SUCCESS: {
      return {
        ...state,
        interestList: action.payload,
      };
    }

    default:
      return state;
  }
};
