import { FETCH_INTEREST_LIST_SUCCESS, FETCH_SKILL_LIST_SUCCESS } from "../../constants/ActionTypes";
import APICaller from "../../utils/APICaller";
import * as APIs from "../../constants/APIs";

export const skillListFetch = () => {
  return (dispatch) => {
    APICaller.execute(APIs.GET_SKILL_LIST).then((response) => {
      dispatch({
        type: FETCH_SKILL_LIST_SUCCESS,
        payload: response.data
      })
    })
  };
};

export const interestListFetch = () => {
  return (dispatch) => {
    APICaller.execute(APIs.GET_INTEREST_LIST).then((response) => {
      dispatch({
        type: FETCH_INTEREST_LIST_SUCCESS,
        payload: response.data
      })
    })
  };
};
