import React from "react";
import { connect } from "react-redux";
import { Card, Table } from "antd";

import { interestListFetch } from "../../redux/actions/Data";

class Interest extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
    };

    this.columns = [
      {
        title: "ID",
        dataIndex: "id",
        width: "5%",
      },
      {
        title: "Interest",
        dataIndex: "title",
        width: "50%",
      }
    ];
  }

  componentDidMount() {
    this.props.interestListFetch();
  }

  render() {
    return (
      <Card className="gx-card" title="Interest List">
        <Table
          columns={this.columns}
          size={"small"}
          dataSource={this.props.interestList || []}
          rowKey={(record) => record.id}
          pagination={false}
          loading={this.state.loading}
          bordered
        />
      </Card>
    );
  }
}

const mapStateToProps = ({ data }) => {
  const { interestList } = data;
  return { interestList };
};

export default connect(
  mapStateToProps,
  { interestListFetch }
)(Interest);
