import { FETCH_COMPANY_LIST_SUCCESS } from "../../constants/ActionTypes";
import APICaller from "../../utils/APICaller";
import * as APIs from "../../constants/APIs";

export const companyListFetch = () => {
  return (dispatch) => {
    APICaller.execute(APIs.GET_COMPANY_LIST).then((response) => {
      dispatch({
        type: FETCH_COMPANY_LIST_SUCCESS,
        payload: response.data,
      });
    });
  };
};

export const companyFetch = (id) => {
  return (dispatch) => {
    return APICaller.execute(APIs.GET_COMPANY(id))
      .then((response) => {
        return response;
      })
      .catch((error) => {
        throw error;
      });
  };
};
