import React from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { Col, Row, message, Card } from "antd";

import { Auxiliary, Spin } from "utils";

import { companyFetch } from "../../redux/actions/Company";

class ViewCompany extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: "",
      company: null,
      notFound: false,
      loading: true,
    };
  }

  async componentDidMount() {
    try {
      let company = await this.props.companyFetch(this.props.match.params.id);

      this.setState({
        id: this.props.match.params.id,
        company,
        loading: false,
      });
    } catch (error) {
      message.error("Company data not found.");
      this.setState({ notFound: true });
    }
  }

  createRenderingData = (toRenderData, data, parentKey) => {
    if (
      data &&
      typeof data === "object" &&
      !(data instanceof Date) &&
      !(data instanceof File)
    ) {
      Object.keys(data).forEach((key) => {
        this.createRenderingData(
          toRenderData,
          data[key],
          parentKey ? `${parentKey}[${key}]` : key
        );
      });
    } else if (data instanceof Date) {
      const value = data.toString();

      toRenderData.push({ key: parentKey, value });
    } else {
      const value = data === null ? "" : data;

      toRenderData.push({ key: parentKey, value });
    }
  };

  render() {
    const { company } = this.state;

    const toRenderData = [];

    console.log(`company:`, company);
    console.log(`toRenderData:`, toRenderData);

    this.createRenderingData(toRenderData, company);

    if (!this.state.notFound) {
      if (this.state.loading) {
        return (
          <Spin tip="Please wait ..." spinning={this.state.loading}></Spin>
        );
      } else {
        return (
          <Auxiliary>
            <div>
              <Row>
                <Col xl={24} lg={24} md={24} sm={48} xs={48}>
                  {company ? (
                    <Card key={company.id} title={company.id}>
                      {toRenderData.map((data) => (
                        <p key={data.key}>
                          <span className="gx-fs-sm gx-font-weight-medium">
                            {data.key}:
                          </span>
                          <span className="gx-ml-2 gx-fs-sm gx-font-weight-light">
                            {data.value}
                          </span>
                        </p>
                      ))}
                    </Card>
                  ) : (
                    <p>company data not available</p>
                  )}
                </Col>
              </Row>
            </div>
          </Auxiliary>
        );
      }
    } else {
      return <Redirect to={"/company/list"} />;
    }
  }
}

const mapStateToProps = ({ auth }) => {
  const { loader, alertMessage, showMessage, authUser } = auth;
  return { loader, alertMessage, showMessage, authUser };
};

export default connect(
  mapStateToProps,
  {
    companyFetch,
  }
)(ViewCompany);
